.socials {
  display: flex;
  justify-content: flex-start;
  padding-bottom: 25px;
}

.SocialMediaShareButton {
  margin-right: 5px;

  svg {
    border-radius: 50%;
    outline: none;

    &:focus,
    rect {
      outline: none;
    }

    &:hover {
      cursor: pointer;
    }
  }
}

.container.post-crud {
  padding: 0px;
  display: flex;
  flex-direction: row;

  form {
    width: 90%;
    font-family: $font_sans;
    font-size: 21px;
    line-height: 1.1;
  }

  .react-tags-wrapper span {
    margin: 18px 12px 18px 0px;
    padding: 10px;
    border: 1px solid skyblue;
    /* margin-bottom: 18px; */
    display: inline;
    flex-wrap: inherit;
    justify-content: inherit;
  }

  .col-md-8 {
    padding: 0px;
  }

  .blog .single.col-md-8 {
    padding-left: 15px;
    padding-right: 15px;
  }

  h2 {
    margin-bottom: 33px;
    margin-top: 50px;
  }

  textarea {
    min-height: 300px;
  }

  input,
  textarea {
    padding: 10px;
    border-radius: 2.5px;
    border: 1px solid skyblue;
    margin-bottom: 20px;

    &:focus {
      outline: none;
      border: 1px solid $blue;
    }
  }

  button {
    width: 100%;
  }

  input[type="file"] {
    font-family: $font_ubuntu;
    padding-right: 5px;
    width: 100%;
  }

  .ReactTags__selected {
    display: flex;
    align-items: center;
    margin-top: 10px;
  }

  .ReactTags__tagInputField {
    margin-bottom: 0px;
  }

  a.ReactTags__remove {
    border: 1px solid skyblue;
    border-radius: 50%;
    padding: 0px 5px 3px;
    margin-left: 7px;
  }

  mark {
    margin: 0px;
    padding: 0px;
  }

  .tag-wrapper {
    border-radius: 3px;

    input {
      margin: 0px auto;
    }
  }

  .ReactTags__tagInput {
    display: flex;
    flex-direction: row;
    max-height: 50px;
  }

  ul {
    list-style: none;
    display: flex;
    margin-left: 15px;
    padding-left: 0px;

    li {
      min-height: 50px;
      display: inline;
      margin-right: 15px;

      span {
        height: 100%;
        width: 100%;
        padding: 0px;
        margin-top: 0px !important;
        margin-bottom: 0px !important;
        margin-right: 0px !important;
      }
    }

    span {
      position: relative;
      border-radius: 3px;
      padding: 0px;
      vertical-align: middle;
    }
  }

  .ReactTags__suggestions {
    display: flex;

    span {
      display: flex;
      height: 50px;
      margin-left: 15px;
    }
  }

  .ReactTags__activeSuggestion {
    height: 50px;
  }

  h3 {
    margin-top: 30px;
    margin-bottom: 20px;
  }

  button {
    padding: 15px 0px;
    font-size: 18px;
  }
}

@media #{$mobile} {
  .container.post-crud {
    flex-direction: column;

    form {
      width: 100%;
    }
  }
}