.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@import url("https://fonts.googleapis.com/css?family=Darker+Grotesque:400,500|Ubuntu:400,500,600,700|Literata:400,500,600|Elsie:900|Bungee+Shade");
@import "./sass/variables.scss";
@import "./sass/login.scss";
@import "./sass/parts/search.scss";

@import "./sass/parts/header.scss";
@import "./sass/parts/footer.scss";
@import "./sass/welcome.scss";
@import "./sass/parts/sidebar.scss";
@import "./sass/parts/carousel.scss";
@import "./sass/blog.scss";
@import "./sass/single-blog.scss";
@import "./sass/single-news.scss";
@import "./sass/news.scss";
@import "./sass/oldschool.scss";
@import "./sass/post.scss";
@import "./sass/contact.scss";

a {
  &:hover {
    text-decoration: none !important;
  }
}

.content {
  // min-height: 1000px;
}


.SocialMediaShareButton {
  outline: none;
}

.content.container {
  padding: 40px 0px 0px 0px !important;
  display: flex;
  align-items: center;

  .body {
    font-family: $font_sans;
    line-height: 1.3;
  }
}

.card {
  border: none !important;
}

.category-header {
  font-family: $font_bungee;
  padding-bottom: 8px;
}

.form {
  display: flex;
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  flex-direction: column;
  justify-content: center;

  input,
  textarea {
    margin-bottom: 22px;
  }

  button {
    width: 100%;
  }
}

.form-control:focus {
  outline: none !important;
}

.bg-light,
nav {
  background-color: #fff !important;
}

.navbar-expand-md .navbar-collapse {
  justify-content: space-between;
}

ul.pagination {
  justify-content: center;
}

@media #{$mobile} {
  .loading {
    width: 90%;
    min-height: 100px;
    margin-left: auto;
    margin-right: auto;
    padding: 90px;
  }

  .container {
    padding-left: 0px !important;
    padding-right: 0px !important;
    width: 95% !important;
    margin-left: auto;
    margin-right: auto;
  }

  .col-md-8-img {
    min-width: 100%;
    width: 100%;
    // margin-bottom: 15px;
    // max-height: 400px;
    // min-height: 400px;
  }

  .blog-list,
  .oldschool-list {
    img {
      min-height: 250px;
      max-height: 250px;
    }
  }

  .blog-list {
    width: 100%;
  }

  .carousel-caption.d-none.d-md-block {
    display: flex !important;
    flex-direction: column;
    bottom: 0px;
    right: 3px;
    left: 3px;
    justify-content: flex-end;
    min-height: 266px;
    max-height: 266px;
    text-align: left;
    padding-bottom: 0px;
    z-index: 0 !important;

    h3 {
      align-self: center;
      font-family: $font_oswald;
      background: rgba(0, 0, 0, 0.8);
      padding: 10px 15px 10px;
      margin-bottom: 50px;
      font-size: 20px;
      color: $old;
    }

    p {
      display: none;
    }
  }

  .controllers {
    display: none !important;
  }
}

.bungee {
  font-family: $font_bungee;
}

.sans {
  font-family: $font_sans;
}

.ubuntu {
  font-family: $font_ubuntu;
}

.blue {
  margin-top: 15px;
  background-color: $blue;
  color: $white;

  &:hover {
    background-color: $white;
    color: $blue;
    border: 1px solid $blue;
  }

  &:focus {
    outline: none !important;
  }
}

.white {
  background-color: $white;
  color: $blue;
  border: 1px solid $blue;

  &:hover {
    background-color: $blue;
    color: $white;
    border: 1px solid $blue;
  }

  &:focus {
    outline: none !important;
  }
}

.blue,
.white {
  text-transform: uppercase;
  font-family: $font_ubuntu;
  padding: 8px 28px;
  font-weight: 600;
  font-size: 12px;
  border-radius: 2px;
  letter-spacing: 1.2pt;
  align-self: start;
}

@media #{$desktop, $tablet} {

  .col-md-8-img {
    min-width: 100%;
    width: 100%;
    margin-bottom: 15px;
    max-height: 400px;
    min-height: 400px;
  }

  .loading {
    width: 80%;
    min-height: 100px;
    margin-left: auto;
    margin-right: auto;
    padding: 100px 200px 300px;
  }
}

@media #{$desktop} {
  .carousel-caption.d-none.d-md-block {
    width: 50%;
    left: 0;
    padding-right: 20px;
    text-align: left;
    bottom: 0px;
    padding-bottom: 0px;
    z-index: 0 !important;

    p {
      margin-bottom: 0px;
    }
  }

  .carousel-indicators {
    width: 50%;
    margin-left: 50% !important;
    padding-bottom: 20px;
  }
}

.active-page {
  background-color: $blue !important;
  color: $white !important;
}

.missing {
  margin-bottom: 50px;

  h2 {
    margin-top: 50px;
    margin-bottom: 50px;
  }

  img {
    min-width: 100%;
    max-width: 100%;
  }
}

.page-link:focus {
  box-shadow: none !important;
  outline: none !important;
}